* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Comic Neue, cursive;
}

body {
  height: 100vh;
  width: 100vw;
  background: #f5f8ee;
}

nav {
  height: 5rem;
  background-color: #de5769;
  justify-content: center;
  display: flex;
}

.title {
  color: #f5f8ee;
  font-size: 3.5rem;
}

main {
  justify-content: center;
  gap: 3rem;
  padding-top: 3rem;
  display: flex;
}

.inputNote {
  width: 30rem;
  color: #000;
  height: auto;
  height: 10rem;
  background-color: #ffd096;
  padding: 1.5rem;
  font-size: 1.5rem;
}

.inputNote:focus {
  border: 1px solid #e99e18;
  box-shadow: 0 0 10px #e99e18;
  outline: none !important;
}

.container {
  flex-direction: column;
  gap: 1.5rem;
  display: flex;
}

.noteHeader {
  width: 100%;
  height: 2rem;
  background-color: #e99e18;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

svg:hover {
  fill: #f5f8ee;
}

.submit {
  background-color: #e99e18;
  border: none;
  margin-left: 1rem;
}

.bi-x {
  margin-left: 1rem;
}

.bi-check {
  margin-right: 1rem;
}

.note {
  width: 30rem;
  color: #000;
  background-color: #ffd096;
}

.note:hover {
  border: 1px solid #e99e18;
  box-shadow: 0 0 10px #e99e18;
}

.noteText {
  margin: 1.5rem;
  padding-bottom: 1.5rem;
  font-size: 1.5rem;
}

.completedNote {
  width: 30rem;
  color: #000;
  background-color: #ffd096;
}

@media only screen and (max-width: 1050px) {
  main {
    flex-direction: column;
    justify-content: center;
    align-self: center;
    align-items: center;
  }

  .container {
    margin: 0;
  }

  .inputNote, .noteText {
    font-size: 1rem;
  }
}

@media only screen and (max-width: 550px) {
  .note, .completedNote, .inputNote {
    width: 20rem;
  }

  .title {
    font-size: 2.5rem;
  }
}

